
.dokan-postbox .toggle-indicator:before {
    content: "\F142";
    display: inline-block;
    font: 400 20px/1 dashicons;
    speak: none;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    text-decoration: none!important
}
.dokan-postbox.closed .toggle-indicator:before {
    content: "\F140";
}
.dokan-postbox {
    position: relative;
}
.dokan-postbox h2.hndle {
    font-size: 14px;
    padding: 8px 12px;
    margin: 0;
    line-height: 1.4;
}

.dokan-loader {
    display: inline-block;
    position: relative;
    width: 64px;
    height: 64px;
}
.dokan-loader div {
    position: absolute;
    border: 4px solid #FF5722;
    opacity: 1;
    border-radius: 50%;
    animation: dokan-loader 1s cubic-bezier(0, 0.2, 0.8, 1) infinite;
}
.dokan-loader div:nth-child(2) {
    animation-delay: -0.5s;
}
@keyframes dokan-loader {
0% {
        top: 28px;
        left: 28px;
        width: 0;
        height: 0;
        opacity: 1;
}
100% {
        top: -1px;
        left: -1px;
        width: 58px;
        height: 58px;
        opacity: 0;
}
}

.dokan-modal-backdrop {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  min-height: 360px;
  background: #000;
  opacity: .7;
  z-index: 99900;
}
.dokan-modal * {
  box-sizing: border-box;
}
.dokan-modal .dokan-modal-content {
  position: fixed;
  background: #fff;
  z-index: 100000;
  left: 50%;
  top: 50%;
  -webkit-transform: translate(-50%, -50%);
  -ms-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
  width: 500px;
}
.dokan-modal .dokan-modal-main.has-footer {
  padding-bottom: 55px;
}
.dokan-modal header.modal-header {
  height: auto;
  background: #fcfcfc;
  padding: 1em 1.5em;
  border-bottom: 1px solid #ddd;
}
.dokan-modal header.modal-header h1 {
  margin: 0;
  padding: 0;
  font-size: 18px;
  font-weight: 700;
  line-height: 1.5em;
}
.dokan-modal header.modal-header .modal-close-link {
  cursor: pointer;
  color: #777;
  height: 54px;
  width: 54px;
  padding: 0;
  position: absolute;
  top: 0;
  right: 0;
  text-align: center;
  border: 0;
  border-left: 1px solid #ddd;
  background-color: transparent;
  -webkit-transition: color 0.1s ease-in-out, background 0.1s ease-in-out;
  transition: color 0.1s ease-in-out, background 0.1s ease-in-out;
}
.dokan-modal header.modal-header .modal-close-link::before {
  font: normal 22px/50px dashicons !important;
  color: #666;
  display: block;
  content: '\F335';
  font-weight: 300;
}
.dokan-modal header.modal-header .modal-close-link:hover {
  background: #ddd;
  border-color: #ccc;
  color: #000;
}
.dokan-modal .modal-body {
  min-height: 100px;
  padding: 15px;
  max-height: 150px;
  overflow-y: scroll;
}
.dokan-modal footer {
  position: absolute;
  left: 0;
  right: 0;
  bottom: -30px;
  z-index: 100;
  padding: 1em 1.5em;
  background: #fcfcfc;
  border-top: 1px solid #dfdfdf;
  -webkit-box-shadow: 0 -4px 4px -4px rgba(0, 0, 0, 0.1);
  box-shadow: 0 -4px 4px -4px rgba(0, 0, 0, 0.1);
}
.dokan-modal footer .inner {
  text-align: right;
  line-height: 23px;
}

.switch {
  position: relative;
  display: inline-block;
  width: 40px;
  height: 18px;
}
.switch input {
  display: none;
}
.switch input:checked + .slider {
  background-color: #64b450;
}
.switch input:checked + .slider:before {
  -webkit-transform: translateX(22px);
  -ms-transform: translateX(22px);
  transform: translateX(22px);
}
.switch input:focus + .slider {
  box-shadow: 0 0 1px #2196F3;
}
.switch .slider {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #ccc;
  -webkit-transition: .4s;
  transition: .4s;
}
.switch .slider:before {
  position: absolute;
  content: "";
  height: 14px;
  width: 14px;
  left: 2px;
  bottom: 2px;
  background-color: white;
  -webkit-transition: .4s;
  transition: .4s;
}
.switch .slider.round {
  border-radius: 34px;
}
.switch .slider.round:before {
  border-radius: 50%;
}
